import angular from 'angular';
import uiRouter from 'angular-ui-router';
import siteIconsComponent from './siteIcons.component';

let siteIconsModule = angular.module('siteIcons', [
  uiRouter
])

.component('siteIcons', siteIconsComponent)

.name;

export default siteIconsModule;



